@use "../utilities/_colors.scss" as *;

// Estilos del footer usando BEM
.footer-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    background-color: $gris-light; // Color de fondo general del footer

    &__content {
        display: flex;
        justify-content: center;
        align-items: center;

        &__icon-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;

            .footer-container__icon-text {
                display: flex;
                align-items: center;
                margin-bottom: 10px;

                .footer-container__icon {
                    width: 50px;
                    height: 50px;
                    background-size: cover;
                    background-position: center;
                    border-radius: 25px;
                }

                .footer-container__text {
                    font-size: 18px;
                    color: #333;
                    margin-left: 10px;
                }
            }

            .footer-container__slogan {
                font-size: 16px;
                color: $gris-dark;
            }
        }
    }

    &__copyright {
        font-size: 14px;
        color: $gris-dark;
        text-align: center;
        padding: 10px 0;

        &__label {
            cursor: pointer;
        }
    }
}
